.contributors {
    .card {
        flex:                       0 0 100%;
        border:                     none;
        text-align:                 center;
        padding:                    1.5em 0;

        .card-img-top {
            width:                  128px;
            height:                 128px;
            display:                block;
            margin:                 auto;
        }

        .card-title {
            padding:                1em 1em 0 1em;
            color:                  $msg-red;
            font-size:              1.175em;
            font-weight:            $font-weight-bold;
        }

        .card-text {
            font-size:              0.875em;
        }

        .btn {
            padding:                0.375em 0.5em;
        }
    }
}
@include media-breakpoint-up(sm) {
    .contributors {
        .card {
            flex:                       0 0 50%;
        }
    }
}
@include media-breakpoint-up(md) {
    .contributors {
        .card {
            flex:                       0 0 33%;
        }
    }
}