.categories {
    .card {
        flex:                           0 0 100%;
        border:                         none;
        padding:                        0 $spacer;
        text-align:                     left;

        .card-title {
            padding:                    0;
            color:                      $msg-red;
            font-size:                  1.0em;
            font-weight:                $font-weight-bold;
        }

        .card-text {
            font-size:                  0.875em;
        }
    }
}
@include media-breakpoint-up(sm) {
    .categories {
        .card {
            flex:                       0 0 50%;
        }
    }
}
@include media-breakpoint-up(md) {
    .categories {
        .card {
            flex:                       0 0 33%;
        }
    }
}
@include media-breakpoint-up(lg) {
    .categories {
        .card {
            flex:                       0 0 25%;
        }
    }
}