.poster {
    .hero-image {
        padding:            1em 0;
    }
    img {
        box-shadow:         $msg-image-shadow;
        width:              80%;
        display:            block;
        transform:          perspective(400px) rotateY(-10deg);
        transition:         $msg-transition;

        &:hover {
            transform:      perspective(400px) rotateY(0deg);
        }
    }
}