@import 'fonts';

/* basic configuration */

$msg-red:                               #860a36;
$msg-gray:                              #f8f8f8;
$msg-grayish:                           #d6d6d6;
$msg-gray-light:                        #969ba2;
$msg-gray-mid:                          #676767;
$msg-gray-dark:                         #4d4d4d;
$msg-black:                             #242424;
$msg-white:                             #f4faff;
$white:                                 #fff !default;
$dark:                                  $msg-gray-dark;

$msg-menu-shadow:                       0 3px 5px 0 rgba(0,0,0,0.2);
$msg-navbar-shadow:                     0 3px 10px 0 rgba(68, 68, 68, 0.1) !important;
$msg-card-shadow:                       0 0 1rem 0 rgba(68, 68, 68, 0.1) !important;
$msg-image-shadow:                      0 0 2rem 0 rgba(68, 68, 68, 0.1) !important;

$msg-menu-border:                       3px solid $msg-red;
$msg-card-border:                       1px solid $msg-grayish;
$msg-link-border:                       1px solid $msg-gray-light;

$font-weight-light:                     300 !default;
$font-weight-normal:                    400 !default;
$font-weight-bold:                      600 !default;
$font-weight-ultrabold:                 800 !default;
$font-family-sans-serif:                "Open Sans", -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji" !default;
$font-size-base:                        1.0625rem;
$font-weight-base:                      $font-weight-light !default;
$font-awesome:                          "FontAwesome";
$body-color:                            $msg-black;

$h2-font-size:                          $font-size-base * 1.5 !default;

$msg-transition:                        all ease-in 0.25s;

/* overriding configration */

$enable-rounded:                        false;
$primary:                               $msg-red;
$link-hover-color:                      $msg-gray-dark;
$link-hover-decoration:                 none;
$spacer:                                1rem * 0.75;
$navbar-padding-y:                      ($spacer) !default;
$jumbotron-bg:                          transparent;

/* bootstrap */

@import 'bootstrap';

/* bootstrap customizations */

.btn {
    text-transform:                     uppercase;
    font-weight:                        $font-weight-light;
}

.dropdown-menu {
    box-shadow:                         $msg-menu-shadow;
}

.jumbotron {
    margin-bottom:                      0;
}

.navbar-nav {
    font-weight:                        $font-weight-normal;

    .nav-item {
        .nav-link {
            color:                      $msg-gray-mid;
            text-transform:             uppercase;
            font-size:                  0.875rem;
            transition:                 $msg-transition;
            padding:                    $spacer * 0.5;

            &:hover {
                color:                  $msg-red;
            }
        }
    }

}
@include media-breakpoint-up(md) {
    .navbar-nav {
        .nav-item {
            .nav-link {
                font-size:              $font-size-base;
                padding:                ($spacer * 0.5) ($spacer * 2);
            }
        }
    }
}

.navbar {
    background-color:                   $white;
    box-shadow:                         $msg-navbar-shadow;

    .navbar-brand {
        img {
            height:                     24px;
        }
    }
    #search-results-dropdown {
        right:                          0;
        padding:                        0;
    }
    #search-box {
        width:                          100%;
    }
}

strong {
    font-weight:                        $font-weight-bold;
}

/* .header, .block, .hero-block */


.hero-block {
    text-align:                         center;
    margin-bottom:                      3rem;
    padding:                            2rem 0;
    .hero-image {
        background-size:                contain;
        background-position:            center center;
        background-repeat:              no-repeat;
    }
    h1 {
        color:                          $msg-gray-dark;
        font-size:                      2.0625rem;
        line-height:                    1.1em;
        font-weight:                    $font-weight-ultrabold;
        margin:                         1rem 0;
        text-transform:                 uppercase;
    }
    h2 {
        color:                          $msg-red;
        font-size:                      1.325rem;
        line-height:                    1.1em;
        font-weight:                    $font-weight-bold;
        margin:                         0 0 1rem 0;
        text-transform:                 unset;
    }
    .lead {
        color:                          $msg-gray-dark;
        font-size:                      1.1rem;
        font-weight:                    $font-weight-normal;
        margin:                         2rem 0;
    }
}
@include media-breakpoint-up(md) {
    .hero-block {
        padding:                        2rem 0;
        text-align:                     left;
        h1 {
            font-size:                  3rem;
        }
        h2 {
            font-size:                  1.625rem;
        }
    }
}

.header {
    text-align:                         center;
    margin-bottom:                      0;
    padding:                            2rem 0.875rem;
    .hero-image {
        background-size:                contain;
        background-position:            center center;
        background-repeat:              no-repeat;
    }
    h1 {
        color:                          $msg-red;
        font-size:                      1.625rem;
        line-height:                    1.1em;
        font-weight:                    $font-weight-ultrabold;
        margin:                         0;
        text-transform:                 uppercase;
    }
    h2 {
        color:                          $msg-gray-dark;
        font-size:                      1.325rem;
        line-height:                    1.1em;
        font-weight:                    $font-weight-bold;
        margin:                         0;
        text-transform:                 unset;
    }
    .lead {
        color:                          $msg-gray-dark;
        font-size:                      1.1rem;
        font-weight:                    $font-weight-normal;
    }
}
@include media-breakpoint-up(md) {
    .header {
        text-align:                     left;
        padding:                        4rem 0;
        h1 {
            font-size:                  2.0625rem;
        }
        h2 {
            font-size:                  1.625rem;
        }
    }
}

.block {
    text-align:                         center;
    margin-bottom:                      0;
    padding:                            2rem 0.875rem;
    .hero-image {
        background-size:                contain;
        background-position:            center center;
        background-repeat:              no-repeat;
    }
    h1 {
        color:                          $msg-red;
        font-size:                      1.625rem;
        line-height:                    1.1em;
        font-weight:                    $font-weight-ultrabold;
        margin:                         1rem 0;
        text-transform:                 uppercase;
    }
    h2 {
        color:                          $msg-gray-dark;
        font-size:                      1.175rem;
        line-height:                    1.1em;
        font-weight:                    $font-weight-bold;
        margin:                         0 0 1rem 0;
        text-transform:                 unset;
    }
    .lead {
        color:                          $msg-gray-dark;
        font-size:                      1.1rem;
        font-weight:                    $font-weight-normal;
        margin:                         2rem 0;
    }
}
@include media-breakpoint-up(md) {
    .block {
        padding:                        4rem 0;
        h1 {
            font-size:                  2.0625rem;
        }
        h2 {
            font-size:                  1.625rem;
        }
    }
    .align-items-center {
        h1,
        h2,
        .lead {
            text-align:                 left;
        }
    }
}

.popover {
    border:                             none;
    border-top:                         $msg-menu-border;
    box-shadow:                         $msg-menu-shadow;

}
.popover-header {
    border:                             none;
    background-color:                   #fff !important;
    color:                              $msg-red;
    text-transform:                     uppercase;
    padding:                            .5rem .75rem 0 .75rem;
}
.popover-body {
    color:                              $msg-gray-mid;
}

/* generics */

.hyphenate {
    word-wrap:                          break-word;
    overflow-wrap:                      break-word;
    white-space:                        normal;
    -webkit-hyphens:                    auto;
    -moz-hyphens:                       auto;
    hyphens:                            auto;
}

.contextual-help {
    font-size:                          0.875em;
}

/* .colophon */

.colophon {
    color:                              $msg-white;
    background-color:                   $msg-gray-dark;
    text-align:                         center;
    font-size:                          $font-size-sm;

    a:not(.btn) {
        color:                          $msg-white;
        border-bottom:                  $msg-link-border;
    }

    div {
        div {
            padding:                    2 * $spacer;
        }
    }

    .social-label {
        display:                        none;
        visibility:                     hidden;
    }

    .btn-network {
        padding:                        .375em .5em; // fixme
    }
}

@include media-breakpoint-up(md) {
    .colophon {
        padding:                        (6 * $spacer) 0;
    }
}


/* .page */

.page {
    color:                              $msg-gray-mid;
    h2 {
        font-weight:                    $font-weight-bold;
        color:                          $msg-gray-dark;
        margin:                         ($spacer * 4) 0;
    }
}

/* #scrollToTop */

#scrollToTop {
    position:                           fixed;
    right:                              $spacer * 1.25;
    bottom:                             $spacer * 1.25;
    opacity:                            0;
    transition:                         $msg-transition;
}
.scrolled {
    #scrollToTop {
        opacity:                        1;
    }
}

/* .attributes */

.attributes {
    .badge {
        font-weight:                    $font-weight-bold;
        text-transform:                 uppercase;
    }
    
    &:not(:last-child) {
        margin-bottom:                  0;
    }
}

@include media-breakpoint-up(md) {
    .attributes {
        dt,
        dd {
            border-top:                 $msg-card-border;
        }
    }
}

/* .category-nav */

.category-nav {
    display:                            block;
    >div {
        padding:                        ($spacer / 3);
    }
    h6 {
        font-size:                      $font-size-base;
    }
    ul {
        display:                        block;
        li {
            a {
                padding:                ($spacer / 3) (1.625rem + ($spacer * 0.225));
                font-size:              0.9em;
                display:                block;
                color:                  $msg-gray-mid;
                &:hover {
                    color:              $msg-red;
                }
                &.current {
                    color:              $msg-red;
                    font-weight:        $font-weight-bold;
                }
            }
        }
    }
}
.btn-category-toggle {
    font-size:                          1.328125rem;
}
@include media-breakpoint-up(md) {
    .side-navigation {
        display:                        block !important;
    }
}
.group-collapser {
    cursor:                             pointer;
    &:before {
        content:                        "\f146";
        font-family:                    $font-awesome;
        padding:                        0 ($spacer * 0.225);
        color:                          $msg-gray-light;
        width:                          1.625rem;
        display:                        inline-block;
    }

    &.collapsed:before {
        content:                        "\f0fe";
    }
}
.sidebar {
    border-bottom:                      $msg-card-border;
    border-right:                       0;
}
@include media-breakpoint-up(sm) {
    .sidebar {
        border-bottom:                  0;
        border-right:                   $msg-card-border;
    }
}

/* .tile */

.tile {
    border:                             0;
}
@include media-breakpoint-up(sm) {
    .tile {
        border-top:                     $msg-card-border;
        border-right:                   0;
        border-bottom:                  0;
        border-left:                    0;
        background-color:               none;
    }
}

/* .dropdown-menu-msg */

.dropdown-menu-msg {
    border:                             none;
    border-top:                         $msg-menu-border;
}

/* #mlmm */

#mlmm {
    width: 100%;
}

#mlmm g,
#mlmm g g,
#mlmm g circle {
    transition: all ease 0.375s;
}

#mlmm g.relations {
    opacity: 0.5;
}

#mlmm:hover g.relations {
    opacity: 0.15;
}

#mlmm g:hover g.relations {
    opacity: 1;
}

#mlmm g:hover g.relations *:not(polygon) {
    stroke-width: 6px;
}

#mlmm g#lanes circle {
    opacity: 0;
}

@import "../_includes/sections/category-cards.scss";
@import "../_includes/sections/contributors.scss";
@import "../_includes/sections/poster.scss";